@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  
}

.title_logo{
  object-fit: contain;
  height:0.3rem;
}
*{
    margin:0;
    padding:0;
}
.app__body{
    display: flex;
}
html{
    font-size:62.5%;
    font-family: 'Roboto', sans-serif;
}
.body{

    flex:0.80 1;
    background-color:#F9F9F9;
    display: flex;
    flex-wrap:wrap;
    width:100vw;
    margin-bottom:2rem;
    background-size: cover;
}
.expand__body{
flex:1 1;

}
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .body{
        flex:0.92 1;
        overflow-y: scroll;
    }
    .expand__body{
        flex:1 1;
        
        }
}
.video__card{
    display: flex;
    flex-direction: column;
    margin:0 0.8rem;
    cursor: pointer;
    
}
.video__card>img{
    width:22.5em;
    height:22.5em;
    object-fit: contain;
    margin-bottom:0;
    padding-bottom: 0;
}
.video__info{
    display:flex;
    align-items: center;
    margin-top:-4rem;
}
.video__info>h2{
    margin-left: 1rem;
    max-width:15rem;
    font-weight:500;
    font-size:1.3rem;
}

.video__channel>h3{
    display: block;
    margin-left: 5rem;
    color:#606060;
    font-weight:400;
    font-size:1.3rem;
}
.video__detailes{
    display: flex;
    align-items: center;
    margin-left: 5rem;
    margin-top:0.5rem;
    
}
.video__detailes>p{
    color:#606060;
    font-weight:300;
    font-size:1.3rem;
    margin-right:0.5rem;
}

@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .video__info>.avatar{
     
        width:4rem;
        height:4rem;
    }

    .video__card>img{
        width:20em;
        height:20em;
        object-fit: contain;
        margin-bottom:0;
        padding-bottom: 0;
    }
    .video__card{
        display: flex;
        flex-direction: column;
        margin:0.5rem;
        cursor: pointer;
        
    }

}
.header{

    display: flex;
    align-items: center;
    background-color:#252525 ;
    justify-content: space-between;
    height:6rem;
    position: -webkit-sticky;
    position: sticky;
    z-index:1;
    top:0;
}
.header__left{
display: flex;
align-items: center;
}
.headerSearch__container{
    display: flex;
    align-items: center;
    flex: 1 1;
    
}
.header__right{
    display: flex;
    align-items: center;
    margin-right:2rem;
}
.header__left>.MuiSvgIcon-root{
    color: white;
    font-size:2.3rem;
    margin:0 3rem;
    cursor: pointer;
}
.headerSearch__icon>.MuiSvgIcon-root{
    color: #727272;
    cursor: pointer;

}
.header__right>.MuiSvgIcon-root{
    color:white;
    margin:0 2rem;
    font-size:2.3rem;
    padding-bottom:0;
    cursor: pointer;
}
.headerSearch__icon>.search_icon{
    color: #727272;
 
    
}

.logo{

    width:90px;
    height:90px;
    object-fit: contain;
    cursor: pointer;
}
.headerSearch__container>input[type=text]{
    border:0.5px solid #72727285;;
    color: #DDDDDD;
    font-size: 1.5rem;
    background-color:#121212;
    width:100%;
    height:3rem;
    padding-left:2rem;

    
}
.headerSearch__container>input:focus{
    outline: 1px solid gray;
    z-index: 1;
    padding-right:20rem;
    
}
.headerSearch__container>input::-webkit-input-placeholder{
    padding-left:1rem;
}
.headerSearch__container>input:-ms-input-placeholder{
    padding-left:1rem;
}
.headerSearch__container>input::-ms-input-placeholder{
    padding-left:1rem;
}
.headerSearch__container>input::placeholder{
    padding-left:1rem;
}
.header__center{
    flex:0.6 1;
}
.headerSearch__icon{

    background-color:#363636;
    height:3rem;
    width:5rem;
    border:0.2px solid #72727285;;
    display: flex;
    align-items: center;
    justify-content: center;

    
}

.header__right>.avatar{
    cursor: pointer;   
}
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    html{
        font-size:30.5%;
    }
    .logo{

        width:40px;
        height:40px;
        margin-left:3rem;
       
    }
    .headerSearch__container>input[type=text]{
        width:100%;
    }
    .header__right{
        margin-right:2rem;
    }
    .header__right>.MuiSvgIcon-root{
        margin:0 0.8rem;
    }
    .header__center{
        flex:0.8 1;
        margin-left:3rem;
    }
    .headerSearch__container>input:focus{
        padding:0;
        outline:none;
    }
    .header__right>.avatar{
        width:3rem;
        height:3rem;
        margin-left:0.5rem;
    }
    
}
.sidebar{
   flex:0.20 1;
    height:100vh;
    background-color:#252525;
    display: flex;
    flex-direction: column;
    padding-top:2rem;
    
    overflow:hidden;
    position: -webkit-sticky;
    position: sticky;
    top:0;
}
.sidebar:hover{
    overflow-y:overlay;
}
.sidebar::-webkit-scrollbar{
    height:0.2rem;
    width: 0.5rem;
  }

  .sidebar::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
    min-height: 10px;
    
  }
.sidebar__row{
display: flex;
align-items: center;
padding:0.8rem 2.5rem;

}
.sidebar__row.active{
    background-color:#7575755b;
    padding-bottom:0.8rem;

}
.sidebar__row.active>h2{
    color: white;
    font-weight:500;
}
.sidebar__row.active>.MuiSvgIcon-root{
    color: #FF0000;
    cursor: pointer;
}
.sidebar__row:hover{
    background-color:#9291915b;
    transition: background-color 0.2s ease-in;
    cursor: pointer;
    
   
}
.sidebar__row:hover>h2{
    color: white;
    transition:color 0.2s ease-in;
}
.sidebar__row:hover>.MuiSvgIcon-root{
    color:#FF0000;
    transition:color 0.2s ease-in;
    cursor: pointer;
}

.sidebar__row>h2{
    color:#727272;

}

.sidebar__row>.MuiSvgIcon-root
{
    color: white;
    font-size:2rem;
    margin-right:2rem;
    cursor: pointer;

}

.divider{
    border-top:0.2px solid rgb(68, 67, 67);
    border-bottom: none;
    border-left: none;
    border-right: none;
    padding:1rem;
}

.Collapse{
    
    transform: translateX(-25rem);
    transition:transform 0.5s ease-in-out;
    flex:0 1;
    transition:flex 0.5s ease-out
    
}

.home-link{
    text-decoration: none;
}
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    .sidebar{
        flex:0.1 1;
    }
  
    .sidebar__row{
        flex-direction: column;
        padding:0.8rem 2.5rem;
        
        }
        .sidebar__row>h2{font-size:0rem;}
        .sidebar__row>.MuiSvgIcon-root{margin-right:0rem;}
        .Collapse{
    
            transform: translateX(-25rem);
            transition:transform 0.5s ease-in-out;
            flex:0 1;
            transition:flex 0.5s ease-out
            
        }
}

.search__results{
    display: flex;
    margin-top:3rem;
    margin-left:5rem;
    
  
  }
  .search__results>.MuiSvgIcon-root{

    font-size:2.5rem;
    color:#796060;
    margin:0 1rem;
  }
  .search__results>h1{
      color:#796060;
      font-size:1.3rem;
      font-weight: 600;
      margin-top:0.5rem;
      
  }
.channelDescription{
display: flex;
flex-direction: column;
margin-left:-7rem;

}
.channelDescription>h1{
    margin-top:3rem;
    margin-bottom:1rem;
}
.channelDescription>hr{
      margin-top:6rem;
      width:60vw;
      color: lightgray;
      opacity:0.3;
  }

  .searchResult{
    display: flex;
    align-items:center;
    margin-bottom:-8rem;
    cursor: pointer;

  }

  .searchResult>img{
    width:22.5em;
    height:22.5em;
    object-fit: contain;
    margin-right:2rem;
  }

  .searchResult_desc>h3{
    margin-bottom: 2rem;
    max-width:40vw;
    color:rgb(95, 95, 95);
    font-weight:500;
    line-height:2rem;
  }
  .searchResult_desc>h2{
    max-width:40vw;
    color: rgba(0, 0, 0, 0.986);
    font-weight:500;
  }
  .searchResult_desc{
    margin-top:-2rem;
  }
  .searchResult_info{
    display: flex;
  }

  .searchResult_info>p{
     margin:1rem 0.5rem;
     font-size:1.2rem;
     color:rgb(95, 95, 95);

  }
@media only screen 
and (min-device-width : 320px) 
and (max-device-width : 480px) {

    
.search__results{
    display: flex;
    margin-top:3rem;
    margin-left:1rem;
    
  
  }
  .searchResult_desc{
    margin-top:2rem;
  }
}
